import "../css/selectPay.css";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  CLICK_PAY_REQUEST,
  CHANGE_STEP_REQUEST,
  CHECK_PAY_REQUEST,
} from "../reducers/matching";
import Pay from "./pay";
const SelectPay = ({ onClose, data }) => {
  const dispatch = useDispatch();
  const { clickPayDone, businessPay } = useSelector((state) => state.matching);
  const businessId = data.businessId;
  const matchingData = data;
  const [btn, setBtn] = useState(true);
  const [openPay, setOpenPay] = useState(false);
  const [amount, setAmount] = useState(0);
  const [sendData, setSendData] = useState({});
  const closePopup = () => {
    setOpenPay(false);
  };
  const submitPay = () => {
    if (businessPay === 0) {
      if (btn) {
        setAmount(25);
      } else {
        setAmount(20);
      }
    } else if (businessPay === "200000") {
      setAmount(5);
    } else {
      setAmount(0);
    }
    // dispatch({
    //   type: CLICK_PAY_REQUEST,
    //   data: {
    //     pay,
    //     businessId,
    //   },
    // });
  };
  useEffect(() => {
    if (amount !== 0) {
      setSendData({ businessId, amount });
      setOpenPay(true);
    }
  }, [amount]);
  useEffect(() => {
    dispatch({
      type: CHECK_PAY_REQUEST,
      data: {
        businessId,
      },
    });
  }, []);
  useEffect(() => {
    if (clickPayDone) {
      window.location.href = "myPageBusiness";
      dispatch({
        type: CHANGE_STEP_REQUEST,
        data: { step: "", matchingData },
      });
    }
  }, [clickPayDone]);
  return (
    <div className="selectPay">
      <div className="article_container">
        <img src="/images/close_btn.png" alt="" onClick={onClose} />
        <p></p>
        {businessPay === 0 ? (
          <div className="select_box">
            <div className="select">
              {btn ? (
                <img src="/images/check_on.png" alt="" />
              ) : (
                <img
                  src="/images/check_off.png"
                  alt=""
                  onClick={() => {
                    setBtn(!btn);
                  }}
                />
              )}
              <p></p>
              <p>250,000</p>
            </div>
            <div className="select">
              {btn ? (
                <img
                  src="/images/check_off.png"
                  alt=""
                  onClick={() => {
                    setBtn(!btn);
                  }}
                />
              ) : (
                <img src="/images/check_on.png" alt="" />
              )}
              <p></p>
              <p>200,000</p>
            </div>
          </div>
        ) : businessPay === "250000" ? (
          <p style={{ textAlign: "center" }}>
                
            <br />
               .
          </p>
        ) : businessPay === "200000" ? (
          <p style={{ textAlign: "center" }}>
             1  5  
            <br />
                .
          </p>
        ) : (
          ""
        )}

        <div className="btn" onClick={submitPay}>
          {businessPay === "250000" ? "" : ""}
        </div>
      </div>
      {openPay ? <Pay onClose={closePopup} data={sendData} /> : ""}
    </div>
  );
};

export default SelectPay;
